import React, { Component } from 'react'
import { connect } from 'react-redux'
import StepZilla from 'react-stepzilla'

import { SelectGoal, CustumizeGoal, Questionnaire, FundAccount } from './steps'
import { getGoals, getGoal, getQuestionnaires, getNodes, getNodeRelationships, getAllocations, getAccountTypes, createAccountt, getUser } from '../store/actions/index'
import SpinnerComponent from '.././components/SpinnerComponent';

import Navbar from '../components/Navbar';

import '../styles/react-stepzilla/react-stepzilla.scss'
import '../styles/steps.scss'
import '../styles/element/css/element.min.css'

const StepTitle = ({ title, description, icon, doneIcon }) => {
  return (
    <span>
      {doneIcon ? <span className={`stepzilla-done-icon ${doneIcon}`} /> : ''}
      <span className={`stepzilla-icon ${icon}`} />
      <span className="stepzilla-title">{title}</span>
      {description ? <span className="stepzilla-desctiption">{description}</span> : ''}
    </span>
  )
}

class AddAccount extends Component {
  constructor(props) {
    super(props)
    this.state = {
      step: 0,
      currentQuestion: {},
      allocation: {},
      selectedGoal: {},
      customGoal: {},
      answerChecked: '',
      loading: false,
      isNextDisabled: true,
      accType: '',
      currency: ''
    }
  }

  componentDidMount = () => {
    const { onGetGoals, onGetQuestionnaires, onGetNodes, onGetNodeRelationships, onGetAllocations, onGetAccountTypes, onGetUser } = this.props;
    const username = localStorage.getItem('username')
    onGetUser({ username: username });
    onGetGoals();
    onGetQuestionnaires();
    onGetNodes();
    onGetNodeRelationships();
    onGetAllocations();
    onGetAccountTypes();
  }

  componentDidUpdate(prevProps) {
    const { accountSubscription, history } = this.props;
    if (accountSubscription !== prevProps.accountSubscription) {
      history.push('/dashboard')
    }
  }

  createAccount = event => {
    const { customGoal, selectedGoal, allocation, currency, accType } = this.state;
    const { onCreateAccount, user, accountTypes } = this.props;
    const form = event.currentTarget;

    event.preventDefault();
    event.stopPropagation();
    const payload = {
      accountNumber: form.elements.accountNumber.value,
      allocationId: allocation.id,
      currencyCode: currency || 'USD',
      accountTypeId: accType || accountTypes[0].id,
      goalId: selectedGoal.id,
      isDecomulation: selectedGoal.is_decumulation,
      decumulationHorizon: customGoal.decumulation_horizon,
      accumulationHorizon: customGoal.accumulation_horizon,
      goalAmount: customGoal.goal_amount,
      clientId: user.id,
      accountName: `${selectedGoal.name} ${allocation.name}`
    }
    console.log('jadi createAccount payload', payload)
    onCreateAccount(payload);
    this.setState({ loading: true })
  }

  customizeGoal = event => {
    const { selectedGoal, customGoal } = this.state;

    if (event.target.name === 'goal_amount') customGoal.goal_amount = parseFloat(event.target.value);
    if (event.target.name === 'horizon' && selectedGoal.is_decumulation) customGoal.decumulation_horizon = parseFloat(event.target.value);
    if (event.target.name === 'horizon' && !selectedGoal.is_decumulation) customGoal.accumulation_horizon = parseFloat(event.target.value);

    if (customGoal.goal_amount && (customGoal.decumulation_horizon || customGoal.accumulation_horizon)) {
      this.setState({ isNextDisabled: false })
    } else {
      this.setState({ isNextDisabled: true })
    }
    this.setState({ customGoal });
  }

  showQuestion = (answerId, jumpToStep) => {
    const { questionnaire, nodes, nodeRelationships, allocations } = this.props;

    const nextNodeRelationships = nodeRelationships.find(nr => nr.answer_id === answerId);
    if (nextNodeRelationships.is_leaf) {
      const allocation = allocations.find(a => {
        const allocationFound = a.node_map.find(nm => nm.node_id === nextNodeRelationships.node_parent_id);
        if (allocationFound) {
          return true
        } else {
          return false
        }
      })
      if (allocation && allocation.id) {
        this.findFirstQuestion(nodes, questionnaire)
        this.setState({ allocation })
        jumpToStep(3)
      }
    } else {
      const nextNode = nodes.find(n => n.id === nextNodeRelationships.node_child_id);
      const nextQuestion = questionnaire.questions.find(q => q.id === nextNode.question_id);
      this.setState({ currentQuestion: nextQuestion });
    }
  }

  handleGoalSelect = goal => {
    this.setState({ selectedGoal: goal });
    this.setState({ isNextDisabled: false })
  }

  findFirstQuestion = () => {
    const { questionnaire, nodes } = this.props;
    console.log('jadi ulaziiiiii')
    const firstNode = nodes.find(n => n.is_first);
    const firstQuestion = questionnaire.questions.find(q => q.id === firstNode.question_id)
    this.setState({ currentQuestion: firstQuestion, allocation: {} });
  }

  disableNext = (isNextDisabled) => {
    this.setState({ isNextDisabled })
  }

  onChangeCurr = (e) => {
    console.log('onChange jadiiiiiii', e);
    this.setState({ currency: e.value})
  }

  onChangeAccountType = (e) => {
    console.log('onChange jadiiiiiii', e);
    this.setState({ accType: e.value})
  }
  

  render() {
    const { currentQuestion, answerChecked, selectedGoal, loading, isNextDisabled, customGoal } = this.state;
    const {  goals, questionnaire, nodes, accountTypes, user, history } = this.props;
    console.log('jadi userrrrrrrr', user)
    const stepzillaDefaultProps = {
      nextButtonCls: `btn btn-primary ${isNextDisabled ? 'disabled' : ''}`,
      backButtonCls: 'btn btn-default',
      nextButtonText: 'Next ',
      backButtonText: ' Back',
      stepsNavigation: false,
    }
    if (Object.entries(currentQuestion).length === 0 && currentQuestion.constructor === Object && questionnaire && nodes.length > 0) this.findFirstQuestion();
    const mainGoals = goals.filter(g => !g.parent_goal_id);
    return !loading && goals.length ? (
      <div>
        <Navbar routerHistory={history} user={user}/>

        <div className="container d-flex flex-column mt-5">
        <div className="react-stepzilla">
            <StepZilla {...stepzillaDefaultProps}
              steps={[
                { name: <StepTitle icon="icon-goal" doneIcon="icon-circle-tick" title="Goal" description="Select your goal" />, component:
                  <SelectGoal
                    goals={mainGoals}
                    handleGoalSelect={this.handleGoalSelect}
                    selectedGoal={selectedGoal}
                  />
                },
                { name: <StepTitle icon="icon-money" doneIcon="icon-circle-tick" title="Customize Goal" description="Tailor goal to your needs" />, component:
                  <CustumizeGoal
                    selectedGoal={selectedGoal}
                    customizeGoal={this.customizeGoal}
                    disableNext={this.disableNext}
                    isNextDisabled={isNextDisabled}
                    customGoal={customGoal}
                  />
                },
                { name: <StepTitle icon="icon-write" doneIcon="icon-circle-tick" title="Questionnaire" description="Answer few questions" />, component:
                  <Questionnaire
                    showQuestion={this.showQuestion}
                    question={currentQuestion}
                    answerChecked={answerChecked}
                    isNextDisabled={isNextDisabled}
                    disableNext={this.disableNext}
                    findFirstQuestion={this.findFirstQuestion}
                  />
                },
                { name: <StepTitle icon="icon-document" doneIcon="icon-circle-tick" title="Account" description="Create your account" />, component:
                  <FundAccount 
                    accountTypes={accountTypes}
                    onChangeCurr={this.onChangeCurr}
                    onChangeAccountType={this.onChangeAccountType}
                    createAccount={this.createAccount}
                  />
                },
              ]}
            />
          </div>
        </div>
      </div>
    ) : (
      <SpinnerComponent />
    )
  }
}

const mapStateToProps = state => ({
  goals: state.goals.goals,
  goal: state.goals.goal,
  username: state.user.username,
  questionnaire: state.questionnaire.questionnaires[0],
  nodes: state.questionnaire.nodes,
  allocations: state.questionnaire.allocations,
  nodeRelationships: state.questionnaire.nodeRelationships,
  accountTypes: state.accounts.accountTypes,
  user: state.user.user,
  accountSubscription: state.accounts.accountSubscription
});

const mapDispatchToProps = dispatch => ({
  onGetGoals: payload => dispatch(getGoals(payload)),
  onGetUser: payload => dispatch(getUser(payload)),
  onGetGoal: payload => dispatch(getGoal(payload)),
  onGetQuestionnaires: payload => dispatch(getQuestionnaires(payload)),
  onGetNodes: payload => dispatch(getNodes(payload)),
  onGetNodeRelationships: payload => dispatch(getNodeRelationships(payload)),
  onGetAllocations: payload => dispatch(getAllocations(payload)),
  onGetAccountTypes: payload => dispatch(getAccountTypes(payload)),
  onCreateAccount: payload => dispatch(createAccountt(payload))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(AddAccount);