import React from 'react';
import { Card, Row, Col, Button, Form } from 'react-bootstrap'
import SpinnerComponent from '../components/SpinnerComponent';
import CreatableSelect from 'react-select/creatable';
import '../styles/steps.scss'
import '../styles/element/css/element.min.css'

export const SelectGoal = ({ goals, handleGoalSelect, selectedGoal }) => (
  <Row>
    <Col md={{ span: 6, offset: 3 }}>
      <Card className="main-card w-100">
        {goals.map(goal => {
          return (
            <Row className="">
              <Card className={`goal-card w-100 ${selectedGoal.id === goal.id ? 'selected' : ''}`} onClick={() => handleGoalSelect(goal)}>
                <span className={`goal-icon ${goal.metadata.image}`}></span>
                <span className="text-big ml-3">{goal.name}</span><br />
              </Card>
            </Row>
          )
        })}
      </Card>
    </Col>
  </Row>
);

export const CustumizeGoal = ({ selectedGoal, customizeGoal, disableNext, customGoal, isNextDisabled }) => {
if(!customGoal.goal_amount && !(customGoal.decumulation_horizon || customGoal.accumulation_horizon) && !isNextDisabled) disableNext(true);
if (customGoal.goal_amount && (customGoal.decumulation_horizon || customGoal.accumulation_horizon) && isNextDisabled) disableNext(false);
return (
  <Row>
    <Col md={{ span: 6, offset: 3 }}>
      <Card className="w-100 p-3 main-card">
        <Card.Header>
        <Row>
            <span className={`goal-icon mx-3 ${selectedGoal.metadata.image}`}></span>
            <h1 className="display-3">{selectedGoal.name}</h1>
          </Row>
        </Card.Header>
        <Card.Body>
          <Row>
            <p className="lead mr-auto">Goal Amount</p>
            <Form.Group>
              <Form.Control onChange={customizeGoal} defaultValue={customGoal.goal_amount} type='number' name="goal_amount"/>
            </Form.Group>
          </Row>
          <Row>
            <p className="lead mr-auto">Period of achievement (years)</p>
            <Form.Group>
              <Form.Control onChange={customizeGoal} defaultValue={customGoal.decumulation_horizon || customGoal.accumulation_horizon} type='number' name="horizon" />
            </Form.Group>
          </Row>
        </Card.Body>
      </Card>
    </Col>
  </Row>
)}

export const Questionnaire = ({ question, showQuestion, jumpToStep, allocationSelected, props, answerChecked, isNextDisabled, disableNext, findFirstQuestion }) => {
  if(!isNextDisabled) disableNext(true);
  // let visible = true;
  // const handleSelect = answerId => {
  //   showQuestion(answerId);
  // }
  return (
    <Row>
      <Col md={{ span: 6, offset: 3 }}>
          {/* <Card className={`main-card ${visible ? 'fadeIn' : 'fadeOut'}`}> */}
          <Card className={`main-card`}>
            <Card.Header as="h6" className="text-center">
              <h2 className="m-3">{question.title}</h2>
            </Card.Header>
            <Card.Body className="questionnaire">
            <div className="custom-controls-stacked">
              {question && question.answers && question.answers.map((answer, index) => {
                return (
                  <Form.Check
                    custom type="radio"
                    className='mb-4 add-account-form-check'
                    checked={answer.id === answerChecked}
                    onChange={() => showQuestion(answer.id, jumpToStep)}
                    id={index + 1}
                    label={answer.label}
                  />
                )
              })}
          </div>
          </Card.Body>
            {/* <Card.Footer className="d-flex flex-column align-items-end">
              <div onClick={showQuestion} className={`questionnaire-btn ${answerChecked ? '' : 'disable-click'}`}>
                <span className="icon-circle-right questionnaire-next float-right"></span>
              </div>
            </Card.Footer> */}
          </Card>
      </Col>
    </Row>
  );
}


export const FundAccount = ({ accountTypes, createAccount, onChangeAccountType, onChangeCurr }) => (
  <Row>
    <Col md={{ span: 6, offset: 3 }}>
      <Card className="main-card">
        <Form onSubmit={createAccount}>
          <Card.Body className="py-4">
            <Form.Row>
              <p className="lead ml-3 mr-auto">Account Number</p>
              <Form.Group>
                <Form.Control name="accountNumber" disabled value={`${Math.floor(100000 + Math.random() * 900000)}${Math.floor(100000 + Math.random() * 900000)}`} />
              </Form.Group>
            </Form.Row>
            <hr className="mt-0 mb-4" />
            <Row>
              <p className="lead ml-3 mr-auto">Currency Code</p>
              <CreatableSelect className="react-select add-account-select mr-1" classNamePrefix="react-select"
                options={[{value: 'USD', label: 'USD'}, {value: 'EUR', label: 'EUR'}]}
                onChange={onChangeCurr}
                defaultValue={{value: 'USD', label: 'USD'}} />
              {/* <Form.Group as={Col} md={4}>
                <Form.Control className="custom-select" name="currency_code" as="select">
                  <option value="USD">USD</option>
                  <option value="EUR">EUR</option>
                </Form.Control>
              </Form.Group> */}
            </Row>
            <hr className="mt-0 mb-4" />
            <Row>
              <p className="lead ml-3 mr-auto">Account Type</p>
              <CreatableSelect className="react-select add-account-select mr-1" classNamePrefix="react-select"
                options={
                  accountTypes.map(type => {
                    return { value: type.id, label: type.name}
                  })
                }
                onChange={onChangeAccountType}
                defaultValue={{value: accountTypes[0].id, label: accountTypes[0].name}} />
              {/* <Form.Group as={Col} md={4}>
                <Form.Control className="custom-select" name="account_type" as="select">
                  {accountTypes.map(type => {
                      return (
                        <option value={type.id}>{type.name}</option>
                      )
                    })}
                </Form.Control>
              </Form.Group> */}
            </Row>
        </Card.Body>
          <Card.Footer className="d-flex justify-content-center">
            <Button variant="primary" type="submit" className="my-3 w-75">Create Account</Button>
          </Card.Footer>
        </Form>
      </Card>
    </Col>
  </Row>
);