import * as actionTypes from './actionTypes'
import axios from 'axios';
import { BASE_PATH } from '../../configuration';

export const getGoalsSuccess = ( res ) => {
  return {
      type: actionTypes.GET_GOALS_SUCCESS,
      goals: res.data.content
  }
}

export const getGoalsFail = ( error ) => {
  return {
      type: actionTypes.GET_GOALS_FAIL,
      error: error
  }
}

export const getGoalsStart = () => {
  return {
      type: actionTypes.GET_GOALS_START
  }
}

export const getGoals = payload => (dispatch, getState) => {
  const { accessToken } = getState().user;
  console.log('jadi getGoals accessToken', accessToken);
  dispatch(getGoalsStart())
  axios({
    method: 'GET',
    headers: {
      'Authorization': `Bearer ${accessToken}`,
      'content-type': 'application/json'
    },
    url: `${BASE_PATH}goal`,
    // params: username ? {
    //   "filter": `username==${username}`
    // } : {}
  })
    .then(res => {
      console.log('jadi getGoals res', res);
      dispatch(getGoalsSuccess(res));
    })
    .catch(err => dispatch(getGoalsFail(err)))
}

export const getGoalSuccess = ( res ) => {
  return {
      type: actionTypes.GET_GOAL_SUCCESS,
      goal: res.data
  }
}

export const getGoalFail = ( error ) => {
  return {
      type: actionTypes.GET_GOAL_FAIL,
      error: error
  }
}

export const getGoalStart = () => {
  return {
      type: actionTypes.GET_GOAL_START
  }
}

export const getGoal = payload => (dispatch, getState) => {
  const { accessToken } = getState().user;
  console.log('jadi getGoal accessToken', accessToken);
  dispatch(getGoalStart())
  axios({
    method: 'GET',
    headers: {
      'Authorization': `Bearer ${accessToken}`,
      'content-type': 'application/json'
    },
    url: `${BASE_PATH}goal/${payload}`
  })
    .then(res => {
      console.log('jadi getGoal res', res);
      dispatch(getGoalSuccess(res));
    })
    .catch(err => dispatch(getGoalFail(err)))
}

export const updateGoalSuccess = ( res ) => {
  return {
      type: actionTypes.UPDATE_GOAL_SUCCESS,
      goal: res.data
  }
}

export const updateGoalFail = ( error ) => {
  return {
      type: actionTypes.UPDATE_GOAL_FAIL,
      error: error
  }
}

export const updateGoalStart = () => {
  return {
      type: actionTypes.UPDATE_GOAL_START
  }
}

export const updateGoal = payload => (dispatch, getState) => {
  const { accessToken, name, type, category, goal_amount, is_decumulation } = getState().user;
  const { id } = payload;
  console.log('jadi updateGoal accessToken', accessToken);
  dispatch(updateGoalStart())
  axios({
    method: 'PUT',
    headers: {
      'Authorization': `Bearer ${accessToken}`,
      'content-type': 'application/json'
    },
    url: `${BASE_PATH}goal/${id}`,
    data: {
      name,
      type,
      category,
      goal_amount,
      is_decumulation    
    }
  })
    .then(res => {
      console.log('jadi updateGoal res', res);
      dispatch(updateGoalSuccess(res));
    })
    .catch(err => dispatch(updateGoalFail(err)))
}

export const getGoalPerformanceSuccess = ( res ) => {
  return {
      type: actionTypes.GET_GOAL_PERFORMANCE_SUCCESS,
      goalPerformance: res.data
  }
}

export const getGoalPerformanceFail = ( error ) => {
  return {
      type: actionTypes.GET_GOAL_PERFORMANCE_FAIL,
      error: error
  }
}

export const getGoalPerformanceStart = () => {
  return {
      type: actionTypes.GET_GOAL_PERFORMANCE_START
  }
}

export const getGoalPerformance = payload => (dispatch, getState) => {
  const { accessToken } = getState().user;
  const { goalId, userId } = payload;
  console.log('jadi getGoalPerformance accessToken', accessToken);
  dispatch(getGoalPerformanceStart())
  axios({
    method: 'GET',
    headers: {
      'Authorization': `Bearer ${accessToken}`,
      'content-type': 'application/json'
    },
    url: `${BASE_PATH}goal/${goalId}/performance?client_id=${userId}&stat=annualized_return_period`,
  })
    .then(res => {
      console.log('jadi getGoalPerformance res', res);
      dispatch(getGoalPerformanceSuccess(res));
    })
    .catch(err => dispatch(getGoalPerformanceFail(err)))
}