import React, { Component } from 'react';
import { Redirect } from 'react-router';
import { connect } from 'react-redux';


import Initializer from './Initializer';
import './App.css';
import { setAccessToken } from './store/actions';
import SpinnerComponent from './components/SpinnerComponent';
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      accessToken: '',
      isAccountExists: false
    }; 
  }
  componentDidMount() {
    const { getAccessToken } = Initializer;
    const { setAccessToken } = this.props;
    getAccessToken((response) => {
      this.setState({ accessToken: response.data.access_token });
      localStorage['accessToken'] = response.data.access_token;
      // setAccessToken(response.data.access_token);
      this.getAccountTypes();
    });
  }

  getAccountTypes = () => {
    const { accessToken } = this.state;
    const { isInitalized } = Initializer;

    isInitalized(accessToken)
      .then((isAccountExists) => {
        isAccountExists && this.setState({ isAccountExists: true });
      });
  }

  redirecting = () => {
    const { isAccountExists } = this.state;
    const { history } = this.props;
    if (isAccountExists) {
      history.push('/initialize');
    } else {
      history.push('/login')
    }
  }
  
  
  render() {
    const { isAccountExists, accessToken } = this.state;
    const { history } = this.props;
    if (accessToken === '') {
      return (
        <SpinnerComponent />
      )
    } else {
      return (
      // isAccountExists.length>0 ?
      //     <Redirect to='/initialize'/>
      //   : <Redirect to='/login' />)
        <Redirect to='/login'  />
      )
    }
  }
}

const mapStateToProps = state => ({
  accessToken: state.user.accessToken
});

const mapDispatchToProps = dispatch => ({
  setAccessToken: payload => dispatch(setAccessToken(payload)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App);